<template>
  <div class="page-content">
    <h1>AppContent</h1>
    
    <!--  -->
    <div class="test-section">
      <h2>Stage</h2>
      <div class="test-container">
        <AppContent
          ref="appContentRef"
          :stages="['', '', '']"
          :footer-config="[
            { nextText: '' },
            {},
            { prevText: '', showNext: false }
          ]"
          :before-next="beforeNextHook"
          :before-prev="beforePrevHook"
          @stage-change="onStageChange"
          @next="onNext"
          @prev="onPrev"
        >
          <template #stage-="{ currentStage, stageName, isFirstStage, isLastStage }">
            <div class="stage-content">
              <h3>{{ stageName }} (Stage {{ currentStage + 1 }})</h3>
              <p></p>
              <div class="form-group">
                <label>:</label>
                <input 
                  v-model="testInput" 
                  placeholder="" 
                  class="form-input"
                />
              </div>
              <div class="form-group">
                <label>:</label>
                <select v-model="processType" class="form-select">
                  <option value="fast"></option>
                  <option value="normal"></option>
                  <option value="detailed"></option>
                </select>
              </div>
              <div class="status-info">
                <p>: {{ testInput }}</p>
                <p>: {{ processType }}</p>
                <p>: {{ isFirstStage }}</p>
                <p>: {{ isLastStage }}</p>
              </div>
            </div>
          </template>
          
          <template #stage-="{ currentStage, stageName }">
            <div class="stage-content">
              <h3>{{ stageName }} (Stage {{ currentStage + 1 }})</h3>
              <p>...</p>
              <div class="processing-display">
                <div class="progress-bar">
                  <div class="progress-fill" :style="{ width: processingProgress + '%' }"></div>
                </div>
                <p>: {{ processingProgress }}%</p>
                <div class="processing-details">
                  <p>: {{ testInput }}</p>
                  <p>: {{ processType }}</p>
                  <p>: {{ processingStatus }}</p>
                  <p>: {{ estimatedTime }}</p>
                </div>
              </div>
            </div>
          </template>
          
          <template #stage-="{ currentStage, stageName }">
            <div class="stage-content">
              <h3>{{ stageName }} (Stage {{ currentStage + 1 }})</h3>
              <p></p>
              <div class="result-display">
                <div class="result-summary">
                  <h4></h4>
                  <p>: {{ testInput }}</p>
                  <p>: {{ processType }}</p>
                  <p>: {{ actualProcessTime }}</p>
                </div>
                <div class="result-data">
                  <h4></h4>
                  <pre>{{ JSON.stringify(processResult, null, 2) }}</pre>
                </div>
                <div class="result-actions">
                  <label>
                    <input type="checkbox" v-model="resultConfirmed" />
                    
                  </label>
                </div>
              </div>
            </div>
          </template>
          
          <template #footer-content-left="{ currentStage, totalStages }">
            <div class="footer-info">
              <span class="stage-indicator">
                 {{ currentStage + 1 }} / {{ totalStages }}
              </span>
              <span class="stage-dots">
                <span 
                  v-for="(stage, index) in stages" 
                  :key="index"
                  class="stage-dot"
                  :class="{ 
                    active: index === currentStage,
                    completed: index < currentStage 
                  }"
                ></span>
              </span>
            </div>
          </template>
        </AppContent>
      </div>
    </div>

    <!--  -->
    <div class="control-panel">
      <h3></h3>
      <div class="control-buttons">
        <button @click="resetToFirst" class="control-btn"></button>
        <button @click="goToStage(1)" class="control-btn"></button>
        <button @click="goToStage(2)" class="control-btn"></button>
        <button @click="toggleNavButtons" class="control-btn">
          {{ showNavButtons ? '' : '' }}
        </button>
        <button @click="simulateProcessing" class="control-btn"></button>
      </div>
      
      <div class="status-panel">
        <h4></h4>
        <div class="status-grid">
          <div class="status-item">
            <label>Stage:</label>
            <span>{{ currentAppStage }}</span>
          </div>
          <div class="status-item">
            <label>:</label>
            <span>{{ showNavButtons ? '' : '' }}</span>
          </div>
          <div class="status-item">
            <label>:</label>
            <span>{{ lastAction }}</span>
          </div>
          <div class="status-item">
            <label>:</label>
            <span>{{ hookValidation ? '' : '' }}</span>
          </div>
        </div>
        
        <div class="hook-controls">
          <label>
            <input type="checkbox" v-model="hookValidation" />
            
          </label>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="event-log">
      <h3></h3>
      <div class="log-container">
        <div 
          v-for="(log, index) in eventLogs" 
          :key="index"
          class="log-entry"
          :class="log.type"
        >
          <span class="log-time">{{ log.time }}</span>
          <span class="log-message">{{ log.message }}</span>
        </div>
      </div>
      <button @click="clearLogs" class="clear-logs-btn"></button>
    </div>
  </div>
</template>

<script>
import AppContent from '@/components/applicationContent/AppContent.vue'

export default {
  name: 'AppContentTest',
  components: {
    AppContent
  },
  data() {
    return {
      // 
      testInput: '',
      processType: 'normal',
      resultConfirmed: false,
      
      // 
      processingProgress: 0,
      processingStatus: '',
      estimatedTime: 0,
      actualProcessTime: 0,
      processResult: null,
      
      // 
      currentAppStage: 0,
      showNavButtons: true,
      lastAction: '',
      hookValidation: false,
      
      // 
      eventLogs: [],
      
      // Stage
      stages: ['', '', '']
    }
  },
  methods: {
    // 
    async beforeNextHook(currentStage, targetStage) {
      this.addLog('info', `beforeNext: ${currentStage} -> ${targetStage}`)
      
      if (!this.hookValidation) {
        return true
      }
      
      // stage
      if (currentStage === 0) {
        // 
        if (!this.testInput.trim()) {
          this.addLog('error', ': ')
          alert('')
          return false
        }
      } else if (currentStage === 1) {
        // 
        if (this.processingProgress < 100) {
          this.addLog('error', ': ')
          alert('')
          return false
        }
      }
      
      return confirm(` "${this.stages[currentStage]}"  "${this.stages[targetStage]}" `)
    },
    
    async beforePrevHook(currentStage, targetStage) {
      this.addLog('info', `beforePrev: ${currentStage} -> ${targetStage}`)
      
      if (!this.hookValidation) {
        return true
      }
      
      return confirm(` "${this.stages[currentStage]}"  "${this.stages[targetStage]}" `)
    },
    
    // 
    onStageChange(newStage, oldStage) {
      this.currentAppStage = newStage
      this.addLog('success', `Stage: ${oldStage} -> ${newStage} (${this.stages[newStage]})`)
      
      // stage
      if (newStage === 1) {
        this.startProcessing()
      }
    },
    
    onNext(currentStage) {
      this.lastAction = ` (Stage ${currentStage})`
      this.addLog('info', `: Stage ${currentStage}`)
    },
    
    onPrev(currentStage) {
      this.lastAction = ` (Stage ${currentStage})`
      this.addLog('info', `: Stage ${currentStage}`)
    },
    
    // 
    resetToFirst() {
      this.$refs.appContentRef.goToStage(0)
      this.resetProcessingState()
      this.addLog('info', '')
    },
    
    goToStage(index) {
      this.$refs.appContentRef.goToStage(index)
      this.addLog('info', `Stage ${index}`)
    },
    
    toggleNavButtons() {
      this.showNavButtons = !this.showNavButtons
      this.addLog('info', `${this.showNavButtons ? '' : ''}`)
    },
    
    // 
    async startProcessing() {
      this.processingProgress = 0
      this.processingStatus = '...'
      this.estimatedTime = this.getEstimatedTime()
      
      const startTime = Date.now()
      
      // 
      const interval = setInterval(() => {
        this.processingProgress += Math.random() * 10
        if (this.processingProgress >= 100) {
          this.processingProgress = 100
          this.processingStatus = ''
          this.actualProcessTime = Math.round((Date.now() - startTime) / 1000)
          this.processResult = this.generateResult()
          clearInterval(interval)
          this.addLog('success', '')
        }
      }, 200)
    },
    
    simulateProcessing() {
      if (this.currentAppStage === 1) {
        this.startProcessing()
      } else {
        this.addLog('warning', '')
      }
    },
    
    resetProcessingState() {
      this.processingProgress = 0
      this.processingStatus = ''
      this.estimatedTime = 0
      this.actualProcessTime = 0
      this.processResult = null
      this.resultConfirmed = false
    },
    
    getEstimatedTime() {
      const timeMap = {
        'fast': 3,
        'normal': 5,
        'detailed': 8
      }
      return timeMap[this.processType] || 5
    },
    
    generateResult() {
      return {
        input: this.testInput,
        type: this.processType,
        processedAt: new Date().toISOString(),
        result: `: ${this.testInput.toUpperCase()}`,
        metadata: {
          processingTime: this.actualProcessTime,
          algorithm: this.processType,
          success: true
        }
      }
    },
    
    // 
    addLog(type, message) {
      this.eventLogs.unshift({
        type,
        message,
        time: new Date().toLocaleTimeString()
      })
      
      // 
      if (this.eventLogs.length > 50) {
        this.eventLogs = this.eventLogs.slice(0, 50)
      }
    },
    
    clearLogs() {
      this.eventLogs = []
      this.addLog('info', '')
    }
  },
  
  mounted() {
    this.addLog('info', 'AppContent')
    this.$emit('update-title', 'AppContent')
  }
}
</script>

<style scoped>
.test-section {
  margin-bottom: 2rem;
  padding: 1.5rem;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  background: white;
}

.test-container {
  height: 500px;
  border: 2px solid #d0d0d0;
  border-radius: 8px;
  background: #fafafa;
}

.stage-content {
  padding: 2rem;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.stage-content h3 {
  margin-top: 0;
  color: #333;
  border-bottom: 2px solid #007bff;
  padding-bottom: 0.5rem;
}

.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: bold;
  color: #555;
}

.form-input, .form-select {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
}

.form-input:focus, .form-select:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
}

.status-info {
  background: #f8f9fa;
  padding: 1rem;
  border-radius: 4px;
  margin-top: 1rem;
}

.status-info p {
  margin: 0.25rem 0;
  font-size: 14px;
}

.processing-display {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.progress-bar {
  width: 100%;
  height: 20px;
  background: #e9ecef;
  border-radius: 10px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #007bff, #0056b3);
  transition: width 0.3s ease;
}

.processing-details {
  background: #e8f4fd;
  padding: 1rem;
  border-radius: 4px;
  border-left: 4px solid #007bff;
}

.result-display {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  height: 100%;
}

.result-summary, .result-data {
  background: #f8f9fa;
  padding: 1rem;
  border-radius: 4px;
  border: 1px solid #dee2e6;
}

.result-actions {
  grid-column: 1 / -1;
  padding: 1rem;
  background: #d4edda;
  border: 1px solid #c3e6cb;
  border-radius: 4px;
  text-align: center;
}

.result-actions label {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  font-weight: bold;
  color: #155724;
}

.footer-info {
  display: flex;
  align-items: center;
  gap: 1rem;
  font-size: 14px;
  color: #666;
}

.stage-indicator {
  font-weight: bold;
}

.stage-dots {
  display: flex;
  gap: 0.5rem;
}

.stage-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #ddd;
  transition: background 0.2s ease;
}

.stage-dot.active {
  background: #007bff;
}

.stage-dot.completed {
  background: #28a745;
}

.control-panel {
  background: white;
  padding: 1.5rem;
  border-radius: 8px;
  border: 1px solid #e0e0e0;
  margin-bottom: 2rem;
}

.control-buttons {
  display: flex;
  gap: 0.75rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

.control-btn {
  padding: 0.5rem 1rem;
  border: 1px solid #007bff;
  background: white;
  color: #007bff;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.2s ease;
}

.control-btn:hover {
  background: #007bff;
  color: white;
}

.status-panel {
  background: #f8f9fa;
  padding: 1rem;
  border-radius: 4px;
  border: 1px solid #dee2e6;
}

.status-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 0.75rem;
  margin-bottom: 1rem;
}

.status-item {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem;
  background: white;
  border-radius: 4px;
  border: 1px solid #e9ecef;
}

.status-item label {
  font-weight: bold;
  color: #495057;
}

.hook-controls {
  padding-top: 1rem;
  border-top: 1px solid #dee2e6;
}

.hook-controls label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
}

.event-log {
  background: white;
  padding: 1.5rem;
  border-radius: 8px;
  border: 1px solid #e0e0e0;
}

.log-container {
  max-height: 300px;
  overflow-y: auto;
  border: 1px solid #e9ecef;
  border-radius: 4px;
  background: #f8f9fa;
  margin-bottom: 1rem;
}

.log-entry {
  display: flex;
  gap: 1rem;
  padding: 0.5rem 1rem;
  border-bottom: 1px solid #e9ecef;
  font-family: monospace;
  font-size: 13px;
}

.log-entry:last-child {
  border-bottom: none;
}

.log-entry.info {
  background: #d1ecf1;
  color: #0c5460;
}

.log-entry.success {
  background: #d4edda;
  color: #155724;
}

.log-entry.warning {
  background: #fff3cd;
  color: #856404;
}

.log-entry.error {
  background: #f8d7da;
  color: #721c24;
}

.log-time {
  font-weight: bold;
  min-width: 80px;
}

.log-message {
  flex: 1;
}

.clear-logs-btn {
  padding: 0.5rem 1rem;
  border: 1px solid #dc3545;
  background: white;
  color: #dc3545;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.2s ease;
}

.clear-logs-btn:hover {
  background: #dc3545;
  color: white;
}

/*  */
@media (max-width: 768px) {
  .result-display {
    grid-template-columns: 1fr;
  }

  .control-buttons {
    flex-direction: column;
  }

  .status-grid {
    grid-template-columns: 1fr;
  }

  .footer-info {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
}
</style>
