<template>
  <div class="filter-tabs-container">
    <div class="horizontal-line top-line"></div>

    <div class="filter-tabs-row">
      <button
        v-for="(tab, index) in tabs"
        :key="index"
        :class="['tab-button', { 'active': activeTab === tab.value }]"
        @click="selectTab(tab.value)"
      >
        {{ tab.label }}
      </button>
    </div>

    <div class="horizontal-line bottom-line"></div>
  </div>
</template>

<script>
export default {
  name: 'FilterTabs', // 
  props: {
    // 
    tabs: {
      type: Array,
      required: true,
      default: () => [
        { label: 'sales and dist', value: 'sales_and_dist' },
        { label: 'person', value: 'person' },
        { label: 'group', value: 'group' },
        { label: 'org', value: 'org' },
        { label: 'company code', value: 'company_code' },
        { label: 'more', value: 'more' },
      ]
    },
    // 
    initialActiveTab: {
      type: [String, Number, Object],
      default: 'sales_and_dist' // 
    }
  },
  data() {
    return {
      activeTab: this.initialActiveTab, // 
    };
  },
  watch: {
    //  initialActiveTab 
    initialActiveTab(newValue) {
      this.activeTab = newValue;
    }
  },
  methods: {
    selectTab(tabValue) {
      if (this.activeTab !== tabValue) {
        this.activeTab = tabValue;
        // 
        this.$emit('tab-selected', tabValue);
        console.log('Tab selected:', tabValue);
      }
    }
  },
  // 
  created() {
    this.$emit('tab-selected', this.activeTab);
  }
};
</script>

<style scoped>
.filter-tabs-container {
  width: 100%;
  background-color: var(--theme-color-darker); /*  */
  padding-bottom: 5px; /*  */
}
.reverse.filter-tabs-container {
  background-color: var(--theme-color-page);
}

.horizontal-line {
  height: 2px; /*  */
  background-color: rgba(255, 255, 255, 0.3); /*  */
  margin: 0 20px; /* padding */
}

.top-line {
  margin-bottom: 10px; /*  */
}

.bottom-line {
  margin-top: 10px; /*  */
}

.filter-tabs-row {
  display: flex;
  justify-content: flex-start; /*  */
  padding: 0 20px; /*  */
  gap: 10px; /*  */
  flex-wrap: wrap; /*  */
}

.middle .filter-tabs-row {
  justify-content: center; /*  */
}

.tab-button {
  background-color: rgba(255, 255, 255, 0.1); /*  */
  color: var(--theme-color-page); /*  */
  border: none;
  border-radius: 5px; /*  */
  padding: 8px 15px;
  cursor: pointer;
  font-size: 0.9em;
  white-space: nowrap; /*  */
  transition: background-color 0.3s ease, color 0.3s ease;
  flex-shrink: 0; /*  */
}
.reverse .tab-button {
  color: var(--theme-color-dark); /*  */
}

.tab-button:hover {
  background-color: rgba(255, 255, 255, 0.2); /*  */
}

.tab-button.active {
  background-color: var(--theme-color-page); /*  */
  color: var(--theme-color-dark); /*  */
  font-weight: bold;
}

.reverse .horizontal-line {
  background-color: rgba(0, 0, 0, 0.5);
}

.reverse .tab-button.active {
  background-color: var(--theme-color-lighter-a);
  color: var(--theme-color-dark);
}

.hide-top-line .top-line,
.hide-bottom-line .bottom-line {
  display: none;
}
</style>