<template>
  <div class="search-test-page">
    <h1>SearchModal </h1>
    
    <div class="test-section">
      <h2></h2>
      
      <!-- 1:  -->
      <div class="test-item">
        <h3>1. </h3>
        <VarBox
          :tree="testTreeWithSearch"
          @update="handleUpdate"
        />
        <div class="result-preview" :key="forceUpdateKey">
          <strong></strong>{{ JSON.stringify(testTreeWithSearch.root?.currentValue) }}
        </div>
      </div>
      
      <!-- 2:  -->
      <div class="test-item">
        <h3>2. searchMethods</h3>
        <VarBox
          :tree="testTreeWithoutSearch"
          @update="handleUpdate"
        />
        <div class="result-preview" :key="forceUpdateKey">
          <strong></strong>{{ JSON.stringify(testTreeWithoutSearch.root?.currentValue) }}
        </div>
      </div>
      
      <!-- 3:  -->
      <div class="test-item">
        <h3>3. </h3>
        <VarBox
          :tree="testTreeMultiSearch"
          @update="handleUpdate"
        />
        <div class="result-preview" :key="forceUpdateKey">
          <strong></strong>{{ JSON.stringify(testTreeMultiSearch.root?.currentValue) }}
        </div>
      </div>

      <!-- 4:  -->
      <div class="test-item">
        <h3>4. </h3>
        <VarBox
          :tree="testTreeCustomHandler"
          @update="handleUpdate"
        />
        <div class="result-preview" :key="forceUpdateKey">
          <strong></strong>{{ JSON.stringify(testTreeCustomHandler.root?.currentValue) }}
        </div>
        <div class="custom-handler-log">
          <strong></strong>
          <div v-for="(log, index) in customHandlerLogs" :key="index" class="log-item">
            {{ log }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { ref } from 'vue'
import VarBox from '@/components/varbox/VarBox.vue'
import { VarTree, createTreeFromConfig, cns, SearchMethod, SearchResultHandler } from '@/utils/VarTree'


const forceUpdateKey = ref(0)
const forceUpdate = () => {
  forceUpdateKey.value++
}

// 
const customHandlerLogs = ref<string[]>([])

// 
const customSearchResultHandler: SearchResultHandler = (data, currentNode) => {
  const timestamp = new Date().toLocaleTimeString()
  customHandlerLogs.value.push(`[${timestamp}] : ${data.method.name}`)
  customHandlerLogs.value.push(`[${timestamp}] : ${JSON.stringify(data.params)}`)
  customHandlerLogs.value.push(`[${timestamp}] : ${data.selectedResults.length}`)

  if (data.firstSelectedResult) {
    customHandlerLogs.value.push(`[${timestamp}] : ${JSON.stringify(data.firstSelectedResult)}`)

    //  name 
    if (data.firstSelectedResult.name) {
      currentNode.currentValue = `: ${data.firstSelectedResult.name}`
      customHandlerLogs.value.push(`[${timestamp}] : ${currentNode.currentValue}`)
    }
  }

  // 
  if (customHandlerLogs.value.length > 10) {
    customHandlerLogs.value = customHandlerLogs.value.slice(-10)
  }
}

// 
const createSearchMethods = (): SearchMethod[] => [
  {
    name: '',
    paramTree: null,
    serviceUrl: '/api/search/mock/simple'
  },
  {
    name: '',
    paramTree: createTreeFromConfig(
      cns('dict', 'dict', 'searchParams', {}, false, {}, [
        cns('string', 'leaf', 'keyword', '', false, {}, [], ''),
        cns('string', 'leaf', 'category', '', false, {}, [], ''),
        cns('date', 'leaf', 'startDate', null, false, {}, [], ''),
        cns('date', 'leaf', 'endDate', null, false, {}, [], ''),
        cns('boolean', 'leaf', 'strict', false, false, {}, [], ''),
      ], '')
    ) as VarTree,
    serviceUrl: '/api/search/mock/advanced'
  },
  {
    name: '',
    paramTree: createTreeFromConfig(
      cns('dict', 'dict', 'bpSearchParams', {}, false, {}, [
        cns('string', 'leaf', 'bpId', '', false, {}, [], 'ID'),
        cns('string', 'leaf', 'companyName', '', false, {}, [], ''),
        cns('selection', 'leaf', 'bpType', '', false, {
          options: ['', '', '']
        }, [], '')
      ], '')
    ) as VarTree,
    serviceUrl: '/api/search/mock/business-partner'
  },
  {
    name: '',
    paramTree: createTreeFromConfig(
      cns('dict', 'dict', 'productSearchParams', {}, false, {}, [
        cns('string', 'leaf', 'productCode', '', false, {}, [], ''),
        cns('string', 'leaf', 'productName', '', false, {}, [], ''),
        cns('number', 'leaf', 'minPrice', null, false, {}, [], ''),
        cns('number', 'leaf', 'maxPrice', null, false, {}, [], '')
      ], '')
    ) as VarTree,
    serviceUrl: '/api/search/mock/product'
  }
]

const createMultiSearchMethods = (): SearchMethod[] => [
  {
    name: '',
    paramTree: null,
    serviceUrl: '/api/search/mock/simple'
  },
  {
    name: '',
    paramTree: createTreeFromConfig(
      cns('dict', 'dict', 'exactParams', {}, false, {}, [
        cns('string', 'leaf', 'id', '', false, {}, [], 'ID'),
        cns('string', 'leaf', 'code', '', false, {}, [], '')
      ], '')
    ) as VarTree,
    serviceUrl: '/api/search/mock/business-partner'
  },
  {
    name: '',
    paramTree: createTreeFromConfig(
      cns('dict', 'dict', 'fuzzyParams', {}, false, {}, [
        cns('string', 'leaf', 'text', '', false, {}, [], ''),
        cns('number', 'leaf', 'similarity', 0.8, false, {}, [], '')
      ], '')
    ) as VarTree,
    serviceUrl: '/api/search/mock/product'
  },
  {
    name: '',
    paramTree: createTreeFromConfig(
      cns('dict', 'dict', 'bpAdvancedParams', {}, false, {}, [
        cns('string', 'leaf', 'bpId', '', false, {}, [], 'ID'),
        cns('string', 'leaf', 'companyName', '', false, {}, [], ''),
        cns('selection', 'leaf', 'bpType', '', false, {
          options: ['', '', '']
        }, [], ''),
        cns('string', 'leaf', 'contactPerson', '', false, {}, [], ''),
        cns('selection', 'leaf', 'status', '', false, {
          options: ['', '', '']
        }, [], '')
      ], '')
    ) as VarTree,
    serviceUrl: '/api/search/mock/business-partner'
  },
  {
    name: '',
    paramTree: createTreeFromConfig(
      cns('dict', 'dict', 'productAdvancedParams', {}, false, {}, [
        cns('string', 'leaf', 'productCode', '', false, {}, [], ''),
        cns('string', 'leaf', 'productName', '', false, {}, [], ''),
        cns('selection', 'leaf', 'category', '', false, {
          options: ['', '', '', '']
        }, [], ''),
        cns('number', 'leaf', 'minPrice', null, false, {}, [], ''),
        cns('number', 'leaf', 'maxPrice', null, false, {}, [], ''),
        cns('number', 'leaf', 'minStock', null, false, {}, [], ''),
        cns('selection', 'leaf', 'status', '', false, {
          options: ['', '', '']
        }, [], '')
      ], '')
    ) as VarTree,
    serviceUrl: '/api/search/mock/product'
  }
]

// 1: VarTree
const testTreeWithSearch = createTreeFromConfig(
  cns('dict', 'dict', 'testData', {}, false, {}, [
    cns('string', 'leaf', 'searchField', '', false, {
      searchMethods: createSearchMethods()
    }, [], '')
  ], '')
)

// 2: VarTree
const testTreeWithoutSearch = createTreeFromConfig(
  cns('dict', 'dict', 'testData', {}, false, {}, [
    cns('string', 'leaf', 'normalField', '', false, {}, [], '')
  ], '')
)

// 3: VarTree
const testTreeMultiSearch = createTreeFromConfig(
  cns('dict', 'dict', 'testData', {}, false, {}, [
    cns('string', 'leaf', 'multiSearchField', '', false, {
      searchMethods: createMultiSearchMethods()
    }, [], '')
  ], '')
)

// 4: VarTree
const testTreeCustomHandler = createTreeFromConfig(
  cns('dict', 'dict', 'testData', {}, false, {}, [
    cns('string', 'leaf', 'customHandlerField', '', false, {
      searchMethods: createSearchMethods(),
      customSearchResultHandler: customSearchResultHandler
    }, [], '')
  ], '')
)

// VarBox
function handleUpdate(payload: any) {
  console.log('VarBox:', payload)
  forceUpdate()
}
</script>

<style scoped>
.search-test-page {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.test-section {
  margin-bottom: 40px;
}

.test-item {
  margin-bottom: 30px;
  padding: 20px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.test-item h3 {
  margin-top: 0;
  color: #333;
}

.result-preview {
  margin-top: 15px;
  padding: 10px;
  background-color: #f0f0f0;
  border-radius: 4px;
  font-family: monospace;
  font-size: 12px;
}

h1 {
  color: var(--theme-color-dark);
  text-align: center;
  margin-bottom: 30px;
}

h2 {
  color: var(--theme-color-light);
  border-bottom: 2px solid var(--theme-color-light);
  padding-bottom: 10px;
}

.custom-handler-log {
  margin-top: 15px;
  padding: 10px;
  background-color: #f8f9fa;
  border-radius: 4px;
  border-left: 4px solid var(--theme-color-dark);
}

.log-item {
  font-family: monospace;
  font-size: 12px;
  color: #666;
  margin: 2px 0;
  padding: 2px 0;
  border-bottom: 1px solid #eee;
}
</style>