                <div class="code-blocks-container">
                  <div class="code-block">
                    <h4> (Script)</h4>
                    <div class="code-example">
                      <pre><code>&lt;script lang="ts" setup&gt;
        import { createTreeFromConfig, cns } from '@/utils/VarTree'

        const extraSlot = createTreeFromConfig(
          cns('string', 'leaf', '', '', false, {}, [], '')
        )

        const handleSearch = (slotProps: any) => {
          alert(`: ${slotProps.allProps.modelValue}`)
        }

        const handleClear = (slotProps: any) => {
          if (slotProps.allProps.node) {
            slotProps.allProps.node.currentValue = ''
          }
        }
        &lt;/script&gt;</code></pre>
                    </div>
                  </div>

                  <div class="code-block">
                    <h4> (Template)</h4>
                    <div class="code-example">
                      <pre><code>&lt;template&gt;
          &lt;VarBox :tree="extraSlot"&gt;
            &lt;template #--extra="slotProps"&gt;
              &lt;button
                class="search-btn"
                @click="handleSearch(slotProps)"
              &gt;
                 
              &lt;/button&gt;
              &lt;button
                class="clear-btn"
                @click="handleClear(slotProps)"
              &gt;
                 
              &lt;/button&gt;
            &lt;/template&gt;
          &lt;/VarBox&gt;
        &lt;/template&gt;</code></pre>
                    </div>
                  </div>

                  <div class="code-block">
                    <h4> (Style)</h4>
                    <div class="code-example">
                      <pre><code>&lt;style scoped&gt;
        .search-btn, .clear-btn {
          margin-left: 8px;
          padding: 6px 12px;
          border: none;
          border-radius: 4px;
          cursor: pointer;
          font-size: 12px;
          transition: background-color 0.3s ease;
        }

        .search-btn {
          background: #28a745;
          color: white;
        }

        .search-btn:hover {
          background: #218838;
        }

        .clear-btn {
          background: #dc3545;
          color: white;
        }

        .clear-btn:hover {
          background: #c82333;
        }
        &lt;/style&gt;</code></pre>
                    </div>
                  </div>
                </div>

                <div class="demo-area">
                  <VarBox :tree="slotExamples.extraSlot">
                    <template #--extra="slotProps">
                      <button class="search-btn" @click="handleSearch(slotProps)"> </button>
                      <button class="clear-btn" @click="handleClear(slotProps)"> </button>
                    </template>
                  </VarBox>
                </div>
              </div>

              <div class="example-container">
                <h3>4.2 --simple </h3>

                <div class="code-blocks-container">
                  <div class="code-block">
                    <h4> (Script)</h4>
                    <div class="code-example">
                      <pre><code>&lt;script lang="ts" setup&gt;
        import { createTreeFromConfig, cns } from '@/utils/VarTree'

        const simpleSlot = createTreeFromConfig(
          cns('number', 'leaf', '', 25, false, {}, [], '')
        )

        const increment = (slotProps: any) => {
          if (slotProps.allProps.node) {
            const currentValue = Number(slotProps.allProps.node.currentValue) || 0
            slotProps.allProps.node.currentValue = currentValue + 1
          }
        }

        const decrement = (slotProps: any) => {
          if (slotProps.allProps.node) {
            const currentValue = Number(slotProps.allProps.node.currentValue) || 0
            slotProps.allProps.node.currentValue = Math.max(0, currentValue - 1)
          }
        }

        const updateValue = (slotProps: any, event: Event) => {
          const target = event.target as HTMLInputElement
          if (slotProps.allProps.node) {
            slotProps.allProps.node.currentValue = Number(target.value)
          }
        }
        &lt;/script&gt;</code></pre>
                    </div>
                  </div>

                  <div class="code-block">
                    <h4> (Template)</h4>
                    <div class="code-example">
                      <pre><code>&lt;template&gt;
          &lt;VarBox :tree="simpleSlot"&gt;
            &lt;template #--simple="slotProps"&gt;
              &lt;div class="custom-number-input"&gt;
                &lt;button @click="decrement(slotProps)"&gt;-&lt;/button&gt;
                &lt;input
                  :value="slotProps.allProps.modelValue"
                  @input="updateValue(slotProps, $event)"
                  type="number"
                  class="number-display"
                /&gt;
                &lt;button @click="increment(slotProps)"&gt;+&lt;/button&gt;
              &lt;/div&gt;
            &lt;/template&gt;
          &lt;/VarBox&gt;
        &lt;/template&gt;</code></pre>
                    </div>
                  </div>

                  <div class="code-block">
                    <h4> (Style)</h4>
                    <div class="code-example">
                      <pre><code>&lt;style scoped&gt;
        .custom-number-input {
          display: flex;
          align-items: center;
          gap: 8px;
          border: 1px solid #ddd;
          border-radius: 4px;
          padding: 4px;
        }

        .custom-number-input button {
          width: 30px;
          height: 30px;
          border: none;
          background: #007bff;
          color: white;
          border-radius: 4px;
          cursor: pointer;
          font-weight: bold;
        }

        .custom-number-input button:hover {
          background: #0056b3;
        }

        .number-display {
          width: 80px;
          text-align: center;
          border: none;
          outline: none;
          font-size: 16px;
        }
        &lt;/style&gt;</code></pre>
                    </div>
                  </div>
                </div>

                <div class="demo-area">
                  <VarBox :tree="slotExamples.simpleSlot">
                    <template #--simple="slotProps">
                      <div class="custom-number-input">
                        <button @click="decrement(slotProps)">-</button>
                        <input
                          :value="slotProps.allProps.modelValue"
                          @input="updateValue(slotProps, $event)"
                          type="number"
                          class="number-display"
                        />
                        <button @click="increment(slotProps)">+</button>
                      </div>
                    </template>
                  </VarBox>
                </div>
              </div>

              <div class="example-container">
