        const updateSelectValue = (slotProps: any, event: Event) => {
          const target = event.target as HTMLSelectElement
          if (slotProps.allProps.node) {
            slotProps.allProps.node.currentValue = target.value
          }
        }
        &lt;/script&gt;</code></pre>
                    </div>
