async function handleExecute(currentStage: number, targetStage: number): Promise<boolean> {
    console.log('handleExecute: stage change:', currentStage, '->', targetStage, 'State:', state.value);

    //  0 (initialScreen) 
    if (currentStage === 0) {
        if (onCreateState.value) {
