        if (onDisplayState.value) {
            // 
            appToState('change');
            appContentRef.value.footerMessage = '';
            return false; //  false  (VarBox )
        } else if (onChangeState.value) {
            // Save Changes API 
            const body = {
                quotation: toRaw(quotationDataTree.root?.currentValue) // 
            };
            const res = await fetch(`${API_BASE_URL}/api/quotation/update`, { //  API
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(body)
            }).then(r => r.json());

            if (res.success) {
                appContentRef.value.footerMessage = `${res.data.quotationData.basicInfo.quotation}!`;
                const newQuotationData: VarNodeValue = res.data.quotationData;
                quotationDataTree.forceSetValue(newQuotationData); // 
                appToState('display'); // 
            } else {
                alert('');
                appContentRef.value.footerMessage = '';
            }
            return false; //  false 
        } else if (onCreateState.value) {
            // Submit Quotation API 
            const body = {
                quotation: toRaw(quotationDataTree.root?.currentValue) // 
            };
            const res = await fetch(`${API_BASE_URL}/api/quotation/create`, { //  API
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(body)
            }).then(r => r.json());
            const quotationData: VarNodeValue = res.data.quotationData; // 
            if (res.success) {
                appContentRef.value.footerMessage = `${res.data.quotationData.basicInfo.quotation}!`;
                appToState('display'); // 
                quotationDataTree.forceSetValue(quotationData); // 
            } else {
                alert('');
                appContentRef.value.footerMessage = '';
            }
            return false; //  false 
        }
    }
    return false; //  AppContent 
}

