<template>
    <div class="single-app-card" @click="handleClick">
        <div class="card-content">
        <slot name="text">
            <span class="app-text">{{ pageName }}</span>
        </slot>
        </div>
        
        <div class="card-icon">
        <slot name="icon">
            <img :src="resolvedIconPath" alt="App Icon" class="default-icon"/>
        </slot>
        </div>
    </div>
</template>

<script>
import { defineComponent,computed } from 'vue';
import { useRouter } from 'vue-router';
import applicationHistoryManager from '@/utils/ApplicationHistoryManager';

// ** require.context() **
// 
//  setup()  context  map
// - :  '@' 
// - :  (false )
// - :  (/\.svg$/  .svg )
const iconContext = require.context('@/assets/icons/', false, /\.svg$/);

//  '@'  Webpack  URL
const iconsMap = {};
iconContext.keys().forEach(key => {
  // `key`  './.svg'  '@/assets/icons/.svg'
  const aliasPath = `@/assets/icons/${key.substring(2)}`; //  './'
  iconsMap[aliasPath] = iconContext(key); //  context  URL
});

export default defineComponent({
    name: 'SingleApp',
    props: {
        /**
         * @description 
         * @type {string}
         */
        pageName: {
        type: String,
        required: true,
        },
        /**
         * @description 
         * @type {string}
         */
        pagePath: {
        type: String,
        required: true,
        },
        /**
         * @description 
         * @type {string}
         */
        iconPath: {
        type: String,
        required: false,
        },
    },
    setup(props) {
        console.log(iconsMap); //  iconsMap 

        const router = useRouter();

        //  computed  iconPath
        const resolvedIconPath = computed(() => {
            //  iconsMap  props.iconPath  URL
            if (props.iconPath && iconsMap[props.iconPath]) {
                return iconsMap[props.iconPath];
            } else {
                //  props.iconPath  iconsMap 
                // 
                console.log(`Could not resolve icon: ${props.iconPath}. Using default icon.`);
                const defaultIconPath = '@/assets/icons/default-app-icon.svg';
                
                //  iconsMap 
                if (iconsMap[defaultIconPath]) {
                    return iconsMap[defaultIconPath];
                } else {
                    //  require.context() 
                    console.error('Default icon itself could not be resolved! Please check "@/assets/icons/default-app-icon.svg" path and require.context configuration.');
                    return ''; // 
                }
            }
        });

        const handleClick = () => {
            //  pagePath 
            if (props.pagePath) {
                // 
                //  pagePath  routePath ( /application )
                const routePath = props.pagePath.startsWith('/application')
                    ? props.pagePath.substring('/application'.length)
                    : props.pagePath;

                const appInfo = {
                    applicationName: props.pageName,
                    routePath: routePath,
                    lore: '', // HomePage
                    tags: [] // HomePage
                };

                applicationHistoryManager.addVisitRecord(appInfo);

                router.push(props.pagePath);
            } else {
                console.warn(`SingleApp: pagePath for "${props.pageName}" is not defined. Cannot navigate.`);
            }
        };

        return {
            handleClick,
            resolvedIconPath, 
        };
    },
});
</script>

<style scoped>
.single-app-card {
    width: 90%; /*  */
    height: 180px; /*  */
    background-color: var(--theme-color-contrast); /*  */
    border-top:1px solid white; /*  */
    border-left:1px solid white; /*  */
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
    justify-content: space-between; /*  */
    align-items: flex-start; /*  */
    padding: 15px;
    position: relative; /*  */
    overflow: hidden; /*  */
    cursor: pointer; /*  */
    transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out; /*  */
}

.single-app-card:hover {
    transform: translateY(-5px); /*  */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3); /*  */
}

.card-content {
    flex-grow: 1; /*  */
    display: flex;
    align-items: flex-start; /*  */
    color: var(--theme-color-card-text); /*  */
    font-size: 1.1em;
    font-weight: bold;
    text-align: left;
    line-height: 1.3;
    overflow: hidden;
    text-overflow: ellipsis;
    width:60%;
}

.app-text {
    /*  */    
    color: var(--theme-color-card-text); /*  */
}

.card-icon {
    position: absolute; /*  */
    right: 15px; /*  15px */
    bottom: 15px; /*  15px */
    width: 40px; /*  */
    height: 40px; /*  */
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: rgba(255, 255, 255, 0.1); /*  */
    border-radius: 5px;
}

.default-icon {
    width: 100%; /*  */
    height: 100%;
    object-fit: contain; /*  */
    /* filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, 0.3)); */
}
</style>